import React, {useState} from 'react';
import InputCustom from "../../Input/InputCustom";
import {Button} from "antd";

const DetailPanel = () => {
    const detailInputState = useState("");
    const numberInputState = useState("");

    return (
        <>
            <div>
                <InputCustom valueState={detailInputState} placeholder={" "}/>
            </div>

            <div>
                <InputCustom valueState={numberInputState} placeholder={" "}/>
            </div>

            <div>
                <Button type="primary" size="large"></Button>
            </div>
        </>
    );
};

export default DetailPanel;