import React, {useState} from 'react';
import InputCustom from "../../Input/InputCustom";
import {Button} from "antd";

const RemainderStoPanel = () => {
    const stoInputState = useState("");
    const remainderInputState = useState("");


    return (
        <>
            <div>
                <InputCustom valueState={stoInputState} placeholder={" "}/>
            </div>

            <div>
                <InputCustom type={"number"} valueState={remainderInputState} placeholder={""}/>
            </div>

            <div>
                <Button type="primary" size="large"></Button>
            </div>
        </>
    );
};

export default RemainderStoPanel;